import React, { useRef } from "react";
import { useFrame } from "@react-three/fiber";
import { useForwardRaycast } from "../hooks";
export default function AnimatedMesh({ animate, children, ...props }) {
  const meshRef = useRef();
  const raycast = useForwardRaycast(meshRef);
  const AnimateFrame = ({ meshRef }) => {
    useFrame(({ clock }, delta) => {
      meshRef?.current && animate({ meshRef, clock, delta, raycast });
    });
    return null;
  };
  return (
    <mesh ref={meshRef} transparent={true}  {...props}>
      {meshRef && animate && <AnimateFrame meshRef={meshRef} />}
      {children}
    </mesh>
  );
}
